import React from 'react';
import { graphql, useStaticQuery, Link } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';


const Cards = () => {
  const data = useStaticQuery(graphql`
    query {
      allMarkdownRemark(filter: {fields: {folderName: {eq: "trend"}}}) {
        nodes {
          fields {
            slug
          }
          frontmatter {
            title
            summary
            image {
              childImageSharp {
                gatsbyImageData(
                  layout: CONSTRAINED
                  aspectRatio: 1.5
                )
              }
            }
          }
        }
      }
    }
  `);

  const cards = data.allMarkdownRemark.nodes;

  return (
    <div>
      <div style={{ 
        display: "flex", 
        justifyContent: "flex-end", 
        alignItems: "center", 
        position: "relative", 
        marginBottom: "20px" 
      }}>
        <hr style={{ width: "100%", position: "absolute" }} />
        <Link to="/trend" style={{ textDecoration: 'none' }}>
        <Button 
           sx={{
            backgroundColor: "black", 
            color: "white", 
           zIndex: "1", 
           textTransform: "none",
           position: "relative",
           '&:hover': {
           backgroundColor: "#555",
            },
            }}
           >
            Trend
         </Button>

        </Link>
      </div>
      <Grid container spacing={3}>
        <Grid item xs={12} sm={12} md={4} lg={3} container spacing={3} direction="column">
          {cards.slice(0,2).map((card, index) => (
            <Grid item xs key={index}>
              <Link to={card.fields.slug} style={{ textDecoration: 'none' }}>
                <Card style={{ height: "100%", display: "flex", flexDirection: "column" }} elevation={0}>
                  <GatsbyImage image={getImage(card.frontmatter.image)} alt={card.frontmatter.title} />
                  <CardContent style={{ flexGrow: 1 }}>
                    <Typography variant="h5" component="div">{card.frontmatter.title}</Typography>
                    <Typography variant="body2">{card.frontmatter.summary}</Typography>
                  </CardContent>
                </Card>
              </Link>
            </Grid>
          ))}
        </Grid>
        <Grid item xs={12} sm={12} md={4} lg={6}>
          <Link to={cards[2].fields.slug} style={{ textDecoration: 'none' }}>
            <Card elevation={0}>
              <GatsbyImage image={getImage(cards[2].frontmatter.image)} alt={cards[2].frontmatter.title} />
              <CardContent>
                <Typography variant="h5" component="div">{cards[2].frontmatter.title}</Typography>
                <Typography variant="body2">{cards[2].frontmatter.summary}</Typography>
              </CardContent>
            </Card>
          </Link>
        </Grid>
        <Grid item xs={12} sm={12} md={4} lg={3} container spacing={3} direction="column">
          {cards.slice(3,5).map((card, index) => (
            <Grid item xs key={index}>
              <Link to={card.fields.slug} style={{ textDecoration: 'none' }}>
                <Card style={{ height: "100%", display: "flex", flexDirection: "column" }} elevation={0}>
                  <GatsbyImage image={getImage(card.frontmatter.image)} alt={card.frontmatter.title} />
                  <CardContent style={{ flexGrow: 1 }}>
                    <Typography variant="h5" component="div">{card.frontmatter.title}</Typography>
                    <Typography variant="body2">{card.frontmatter.summary}</Typography>
                  </CardContent>
                </Card>
              </Link>
            </Grid>
          ))}
        </Grid>
      </Grid>
    </div>
  );
};

export default Cards;

