import React from 'react';
import { Link } from 'gatsby';
import { Grid, Button, makeStyles, Tooltip, Typography } from '@material-ui/core';
import GridListTileComponent from '../components/GridListTileComponent';

// Define your custom styles in a makeStyles hook
const useStyles = makeStyles(() => ({
  button: {
    backgroundColor: "black",
    color: "white",
    zIndex: 1,
    textTransform: "none",
    position: "relative",
    '&:hover': {
      backgroundColor: "#555", // Set the color you want when the button is hovered
    },
  },
  folderLine: {
    display: "flex", 
    justifyContent: "flex-end", 
    alignItems: "center", 
    position: "relative", 
    marginBottom: "20px"
  },
  hr: {
    width: "100%", 
    position: "absolute"
  },
  tooltip: {
    fontSize: "1em",
  },
}));

// Custom function to convert strings to title case
function toTitleCase(str) {
  return str.replace(/\w\S*/g, function(txt){
    return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
  });
}

export default function HomeComponent({ folders }) {
  const classes = useStyles();

  return (
    <>
      {Object.entries(folders).map(([folderName, nodes]) => (
        <>
          <div className={classes.folderLine}>
            <hr className={classes.hr} />
            <Tooltip title={<Typography className={classes.tooltip}>Click For all articles </Typography>}>
              <Button className={classes.button} component={Link} to={`/${folderName}`}>
                {toTitleCase(folderName)}
              </Button>
            </Tooltip>
          </div>
          <Grid container spacing={3}>
            {nodes.slice(0, 4).map(({ node }) => (
              <GridListTileComponent key={node.id} node={node} />
            ))}
          </Grid>
        </>
      ))}
    </>
  )
}
