import React from 'react';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import { Link } from 'gatsby';
import { Typography, Tooltip, Grid, Card, CardContent, Divider } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(() => ({
  card: {
    maxWidth: 345,
    boxShadow: 'none',
    position: 'relative',
  },
  media: {
    height: 140,
  },
  tooltip: {
    backgroundColor: '#c62828',
    fontSize: '0.7em',
  },
  link: {
    textDecoration: 'none',
  },
  divider: {
    margin: '10px 0',
  },
  freeText: {
    position: 'absolute',
    top: '10px',
    right: '10px',
    backgroundColor: 'transparent',
    color: 'red',
    padding: '2px 5px',
    fontSize: '0.7em',
    fontWeight: 'bold',
    zIndex: 1,
    "&::before": {
      content: "''",
      position: 'absolute',
      top: '15%', // Offset the border from the top of the container
      bottom: 0,
      left: '-1px',
      borderLeft: '2px solid red',
      height: '60%', // Set the height of the border
    },
  },
}));

const GridListTileComponent = ({ node }) => {
  const classes = useStyles();

  return (
    <Grid item xs={12} sm={6} md={4} lg={3}>
      <Link to={node.fields.slug} className={classes.link}>
        <Tooltip title={node.frontmatter.title} classes={{ tooltip: classes.tooltip }}>
          <Card className={classes.card}>
            <div className={classes.freeText}>{node.frontmatter.article}</div>
            <GatsbyImage
              image={getImage(node.frontmatter.image)}
              alt={node.frontmatter.title}
              className={classes.media}
            />
            <CardContent>
              <Typography variant="h5" component="h2">
                {node.frontmatter.title}
              </Typography>
              <Divider className={classes.divider} />
              <Typography variant="body2" color="textSecondary" component="p">
                {node.frontmatter.summary}
              </Typography>
            </CardContent>
          </Card>
        </Tooltip>
      </Link>
    </Grid>
  );
};

export default GridListTileComponent;
