import React from 'react';
import { graphql } from 'gatsby';
import groupBy from 'lodash/groupBy';
import HomeComponent from '../components/HomeComponent';
import Cards from '../components/Cards';
import Layout from '../components/layout';

const IndexPage = ({ data }) => {
  const folders = groupBy(data.allMarkdownRemark.edges, 'node.fields.folderName');
  return (
    <Layout>
      <Cards />
      <HomeComponent folders={folders} />
    </Layout>
  );
};

export default IndexPage;

export const query = graphql`
  query {
    allMarkdownRemark(filter: {fields: {folderName: {ne: "trend"}}}) {
      edges {
        node {
          fields {
            slug
            folderName
          }
          frontmatter {
            title
            article
            summary
            image {
              childImageSharp {
                gatsbyImageData(
                  layout: CONSTRAINED
                  placeholder: BLURRED
                  aspectRatio: 2.33
                  width: 900
                )
              }
            }
          }
        }
      }
    }
  }
`;
